import { useState } from "react";
import { useTranslation } from "react-i18next";
import { Check, ChevronsUpDown, Calendar } from "lucide-react";
import { useIsMobile } from "@/hooks/use-mobile";
import { useLocalizedContent } from "@/hooks/useLocalizedContent";
import { cn } from "@/lib/utils";
import { Button } from "@/components/ui/button";
import { ScrollArea } from "@/components/ui/scroll-area";
import {
  Drawer,
  DrawerContent,
  DrawerHeader,
  DrawerTitle,
  DrawerFooter,
} from "@/components/ui/drawer";
import {
  Popover,
  PopoverContent,
  PopoverTrigger,
} from "@/components/ui/popover";
import {
  Command,
  CommandGroup,
  CommandItem,
  CommandList,
} from "@/components/ui/command";

interface Activity {
  id: string;
  title_zh?: string;
  title_en?: string;
  title_pt?: string;
  status?: string;
}

interface ActivitySelectDrawerProps {
  open: boolean;
  onOpenChange: (open: boolean) => void;
  value: string;
  onSelect: (activityId: string) => void;
  activities: Activity[];
}

const ActivitySelectDrawer = ({
  open,
  onOpenChange,
  value,
  onSelect,
  activities,
}: ActivitySelectDrawerProps) => {
  const { t } = useTranslation();
  const { getLocalizedField } = useLocalizedContent();
  const isMobile = useIsMobile();
  const [pendingValue, setPendingValue] = useState(value);

  // Reset pending value when drawer opens
  const handleOpenChange = (newOpen: boolean) => {
    if (newOpen) {
      setPendingValue(value);
    }
    onOpenChange(newOpen);
  };

  // Get selected activity name
  const getSelectedName = () => {
    if (!value) return "";
    const selected = activities?.find((a) => a.id === value);
    return selected ? getLocalizedField(selected, "title") : "";
  };

  // Handle confirm (mobile only)
  const handleConfirm = () => {
    onSelect(pendingValue);
    onOpenChange(false);
  };

  // Handle select (desktop - immediate)
  const handleDesktopSelect = (activityId: string) => {
    onSelect(activityId);
    onOpenChange(false);
  };

  // Trigger button
  const triggerButton = (
    <button
      type="button"
      className="flex items-center gap-1 text-sm hover:text-primary transition-colors"
      onClick={() => handleOpenChange(true)}
    >
      {value ? (
        <span className="text-primary">{getSelectedName()}</span>
      ) : (
        <span className="text-muted-foreground">
          {t("community.linkActivity", "å…³è”æ´»åŠ¨")}
        </span>
      )}
      <ChevronsUpDown className="h-4 w-4 opacity-50" />
    </button>
  );

  // Mobile: Full-screen Drawer
  if (isMobile) {
    return (
      <>
        {triggerButton}
        <Drawer open={open} onOpenChange={handleOpenChange}>
          <DrawerContent className="max-h-[85vh] flex flex-col">
            <DrawerHeader className="pb-2">
              <DrawerTitle>{t("community.selectActivity", "é€‰æ‹©æ´»åŠ¨")}</DrawerTitle>
            </DrawerHeader>

            {/* Activity List */}
            <ScrollArea className="flex-1 px-4">
              <div className="space-y-1 pb-4">
                {/* None option */}
                <div
                  className={cn(
                    "flex items-center gap-3 py-3 px-3 rounded-lg cursor-pointer transition-colors",
                    pendingValue === "" ? "bg-primary/10" : "hover:bg-muted"
                  )}
                  onClick={() => setPendingValue("")}
                >
                  <Check
                    className={cn(
                      "h-4 w-4 shrink-0",
                      pendingValue === "" ? "opacity-100 text-primary" : "opacity-0"
                    )}
                  />
                  <span className="text-muted-foreground">
                    {t("community.noActivity", "ä¸å…³è”æ´»åŠ¨")}
                  </span>
                </div>

                {/* Activity items */}
                {activities.map((activity) => (
                  <div
                    key={activity.id}
                    className={cn(
                      "flex items-center gap-3 py-3 px-3 rounded-lg cursor-pointer transition-colors",
                      pendingValue === activity.id ? "bg-primary/10" : "hover:bg-muted"
                    )}
                    onClick={() => setPendingValue(activity.id)}
                  >
                    <Check
                      className={cn(
                        "h-4 w-4 shrink-0",
                        pendingValue === activity.id ? "opacity-100 text-primary" : "opacity-0"
                      )}
                    />
                    <div className="flex items-center gap-2">
                      <Calendar className="h-4 w-4 text-muted-foreground shrink-0" />
                      <span className="font-medium">
                        {getLocalizedField(activity, "title")}
                      </span>
                    </div>
                  </div>
                ))}

                {activities.length === 0 && (
                  <div className="py-6 text-center text-muted-foreground">
                    {t("activities.noActivities", "æš‚æ— å¯ç”¨æ´»åŠ¨")}
                  </div>
                )}
              </div>
            </ScrollArea>

            {/* Footer Buttons */}
            <DrawerFooter className="pt-2">
              <div className="flex gap-3">
                <Button
                  variant="outline"
                  className="flex-1"
                  onClick={() => handleOpenChange(false)}
                >
                  {t("common.cancel", "å–æ¶ˆ")}
                </Button>
                <Button className="flex-1" onClick={handleConfirm}>
                  {t("community.confirmSelection", "ç¡®è®¤é€‰æ‹©")}
                </Button>
              </div>
            </DrawerFooter>
          </DrawerContent>
        </Drawer>
      </>
    );
  }

  // Desktop: Popover with Command
  return (
    <Popover open={open} onOpenChange={handleOpenChange}>
      <PopoverTrigger asChild>{triggerButton}</PopoverTrigger>
      <PopoverContent className="w-72 p-0" align="start">
        <Command>
          <CommandList>
            <CommandGroup>
              <CommandItem
                value="none"
                onSelect={() => handleDesktopSelect("")}
              >
                <Check
                  className={cn(
                    "mr-2 h-4 w-4",
                    !value ? "opacity-100" : "opacity-0"
                  )}
                />
                {t("community.noActivity", "ä¸å…³è”æ´»åŠ¨")}
              </CommandItem>
              {activities.map((activity) => (
                <CommandItem
                  key={activity.id}
                  value={activity.id}
                  onSelect={() => handleDesktopSelect(activity.id)}
                >
                  <Check
                    className={cn(
                      "mr-2 h-4 w-4",
                      value === activity.id ? "opacity-100" : "opacity-0"
                    )}
                  />
                  {getLocalizedField(activity, "title")}
                </CommandItem>
              ))}
            </CommandGroup>
          </CommandList>
        </Command>
      </PopoverContent>
    </Popover>
  );
};

export default ActivitySelectDrawer;
