import { useState, useEffect } from "react";
import { useTranslation } from "react-i18next";
import { Check, ChevronsUpDown, MapPin, Search } from "lucide-react";
import { useIsMobile } from "@/hooks/use-mobile";
import { useLocalizedContent } from "@/hooks/useLocalizedContent";
import { cn } from "@/lib/utils";
import { Button } from "@/components/ui/button";
import { Input } from "@/components/ui/input";
import {
  Drawer,
  DrawerContent,
  DrawerHeader,
  DrawerTitle,
  DrawerFooter,
} from "@/components/ui/drawer";
import {
  Popover,
  PopoverContent,
  PopoverTrigger,
} from "@/components/ui/popover";
import {
  Command,
  CommandEmpty,
  CommandGroup,
  CommandInput,
  CommandItem,
  CommandList,
} from "@/components/ui/command";

interface Restaurant {
  id: string;
  name_zh?: string;
  name_en?: string;
  name_pt?: string;
  address?: string;
  type?: string | string[];
}

interface RestaurantSelectDrawerProps {
  open: boolean;
  onOpenChange: (open: boolean) => void;
  value: string;
  onSelect: (restaurantId: string) => void;
  restaurants: Restaurant[];
  showNoneOption?: boolean;
  required?: boolean;
}

const RestaurantSelectDrawer = ({
  open,
  onOpenChange,
  value,
  onSelect,
  restaurants,
  showNoneOption = true,
  required = false,
}: RestaurantSelectDrawerProps) => {
  const { t } = useTranslation();
  const { getLocalizedField } = useLocalizedContent();
  const isMobile = useIsMobile();
  const [search, setSearch] = useState("");
  const [pendingValue, setPendingValue] = useState(value);
  const [drawerHeight, setDrawerHeight] = useState<string>('85vh');

  // Monitor visualViewport to dynamically adjust drawer height
  useEffect(() => {
    if (!open || !isMobile) return;
    
    const viewport = window.visualViewport;
    if (!viewport) {
      setDrawerHeight('85vh');
      return;
    }
    
    // Calculate initial max height (85% of viewport)
    const updateHeight = () => {
      // Use actual viewport height in pixels for accurate sizing
      const availableHeight = viewport.height;
      const targetHeight = Math.min(availableHeight * 0.85, availableHeight - 50);
      setDrawerHeight(`${targetHeight}px`);
    };
    
    // Set initial height
    updateHeight();
    
    // Listen for viewport changes (keyboard show/hide)
    viewport.addEventListener('resize', updateHeight);
    
    return () => {
      viewport.removeEventListener('resize', updateHeight);
    };
  }, [open, isMobile]);

  // Reset pending value when drawer opens
  const handleOpenChange = (newOpen: boolean) => {
    if (newOpen) {
      setPendingValue(value);
      setSearch("");
    }
    onOpenChange(newOpen);
  };

  // Filter restaurants based on search
  const filteredRestaurants = restaurants?.filter((r) => {
    if (!search) return true;
    const name = getLocalizedField(r, "name")?.toLowerCase() || "";
    const address = r.address?.toLowerCase() || "";
    const searchLower = search.toLowerCase();
    return name.includes(searchLower) || address.includes(searchLower);
  }) || [];

  // Get selected restaurant name
  const getSelectedName = () => {
    if (!value) return "";
    const selected = restaurants?.find((r) => r.id === value);
    return selected ? getLocalizedField(selected, "name") : "";
  };

  // Handle confirm (mobile only)
  const handleConfirm = () => {
    onSelect(pendingValue);
    onOpenChange(false);
    setSearch("");
  };

  // Handle select (desktop - immediate)
  const handleDesktopSelect = (restaurantId: string) => {
    onSelect(restaurantId);
    onOpenChange(false);
    setSearch("");
  };

  // Trigger button
  const triggerButton = (
    <button
      type="button"
      className="flex items-center gap-1 text-sm hover:text-primary transition-colors"
      onClick={() => handleOpenChange(true)}
    >
      {value ? (
        <span className="text-primary">{getSelectedName()}</span>
      ) : (
        <span className="text-muted-foreground">
          {t("community.linkRestaurant", "å…³è”é¤å…")}
          {required && <span className="text-destructive ml-1">*</span>}
        </span>
      )}
      <ChevronsUpDown className="h-4 w-4 opacity-50" />
    </button>
  );

  // Mobile: Full-screen Drawer
  if (isMobile) {
    return (
      <>
        {triggerButton}
        <Drawer open={open} onOpenChange={handleOpenChange}>
          <DrawerContent 
            className="flex flex-col"
            style={{ 
              height: drawerHeight,
              maxHeight: drawerHeight,
              transition: 'height 0.15s ease-out, max-height 0.15s ease-out'
            }}
          >
            <DrawerHeader className="pb-2 shrink-0">
              <DrawerTitle>{t("community.selectRestaurant", "é€‰æ‹©é¤å…")}</DrawerTitle>
            </DrawerHeader>

            {/* Search Input */}
            <div className="px-4 pb-3 shrink-0">
              <div className="relative">
                <Search className="absolute left-3 top-1/2 -translate-y-1/2 h-4 w-4 text-muted-foreground" />
                <Input
                  placeholder={t("community.searchRestaurant", "æœç´¢é¤å…...")}
                  value={search}
                  onChange={(e) => setSearch(e.target.value)}
                  className="pl-9"
                />
              </div>
            </div>

            {/* Restaurant List - use flex-1 and overflow for proper sizing */}
            <div className="flex-1 overflow-y-auto px-4 min-h-0">
              <div className="space-y-1 pb-4">
                {/* None option */}
                {showNoneOption && (
                  <div
                    className={cn(
                      "flex items-center gap-3 py-3 px-3 rounded-lg cursor-pointer transition-colors",
                      pendingValue === "" ? "bg-primary/10" : "hover:bg-muted"
                    )}
                    onClick={() => setPendingValue("")}
                  >
                    <Check
                      className={cn(
                        "h-4 w-4 shrink-0",
                        pendingValue === "" ? "opacity-100 text-primary" : "opacity-0"
                      )}
                    />
                    <span className="text-muted-foreground">
                      {t("community.noRestaurantLink", "ä¸å…³è”é¤å…")}
                    </span>
                  </div>
                )}

                {/* Restaurant items */}
                {filteredRestaurants.map((restaurant) => (
                  <div
                    key={restaurant.id}
                    className={cn(
                      "flex items-start gap-3 py-3 px-3 rounded-lg cursor-pointer transition-colors",
                      pendingValue === restaurant.id ? "bg-primary/10" : "hover:bg-muted"
                    )}
                    onClick={() => setPendingValue(restaurant.id)}
                  >
                    <Check
                      className={cn(
                        "h-4 w-4 shrink-0 mt-0.5",
                        pendingValue === restaurant.id ? "opacity-100 text-primary" : "opacity-0"
                      )}
                    />
                    <div className="flex-1 min-w-0">
                      <p className="font-medium truncate">
                        {getLocalizedField(restaurant, "name")}
                      </p>
                      {restaurant.address && (
                        <p className="text-sm text-muted-foreground truncate flex items-center gap-1 mt-0.5">
                          <MapPin className="h-3 w-3 shrink-0" />
                          {restaurant.address}
                        </p>
                      )}
                    </div>
                  </div>
                ))}

                {filteredRestaurants.length === 0 && search && (
                  <div className="py-6 text-center text-muted-foreground">
                    {t("community.noRestaurantFound", "æœªæ‰¾åˆ°åŒ¹é…çš„é¤å…")}
                  </div>
                )}
              </div>
            </div>

            {/* Footer Buttons */}
            <DrawerFooter className="pt-2">
              <div className="flex gap-3">
                <Button
                  variant="outline"
                  className="flex-1"
                  onClick={() => handleOpenChange(false)}
                >
                  {t("common.cancel", "å–æ¶ˆ")}
                </Button>
                <Button className="flex-1" onClick={handleConfirm}>
                  {t("community.confirmSelection", "ç¡®è®¤é€‰æ‹©")}
                </Button>
              </div>
            </DrawerFooter>
          </DrawerContent>
        </Drawer>
      </>
    );
  }

  // Desktop: Popover with Command
  return (
    <Popover open={open} onOpenChange={handleOpenChange}>
      <PopoverTrigger asChild>{triggerButton}</PopoverTrigger>
      <PopoverContent className="w-72 p-0" align="start">
        <Command shouldFilter={false}>
          <CommandInput
            placeholder={t("community.searchRestaurant", "æœç´¢é¤å…...")}
            value={search}
            onValueChange={setSearch}
          />
          <CommandList>
            <CommandEmpty>{t("community.noRestaurantFound", "æœªæ‰¾åˆ°åŒ¹é…çš„é¤å…")}</CommandEmpty>
            <CommandGroup>
              {showNoneOption && (
                <CommandItem
                  value="none"
                  onSelect={() => handleDesktopSelect("")}
                >
                  <Check
                    className={cn(
                      "mr-2 h-4 w-4",
                      !value ? "opacity-100" : "opacity-0"
                    )}
                  />
                  {t("community.noRestaurantLink", "ä¸å…³è”é¤å…")}
                </CommandItem>
              )}
              {filteredRestaurants.map((restaurant) => (
                <CommandItem
                  key={restaurant.id}
                  value={restaurant.id}
                  onSelect={() => handleDesktopSelect(restaurant.id)}
                >
                  <Check
                    className={cn(
                      "mr-2 h-4 w-4",
                      value === restaurant.id ? "opacity-100" : "opacity-0"
                    )}
                  />
                  {getLocalizedField(restaurant, "name")}
                </CommandItem>
              ))}
            </CommandGroup>
          </CommandList>
        </Command>
      </PopoverContent>
    </Popover>
  );
};

export default RestaurantSelectDrawer;
