const EmojiPicker = ({ onSelect }: EmojiPickerProps) => {
  const { t } = useTranslation();
  const isMobile = useIsMobile();
  const [open, setOpen] = useState(false);
  const [activeCategory, setActiveCategory] = useState(0);

  const handleSelect = (emoji: string) => {
    onSelect(emoji);
    if (!isMobile) {
      setOpen(false);
    }
  };

  const categoryLabels: Record<string, string> = {
    smileys: t("emoji.smileys", "è¡¨æƒ…"),
    gestures: t("emoji.gestures", "æ‰‹åŠ¿"),
    food: t("emoji.food", "ç¾é£Ÿ"),
    hearts: t("emoji.hearts", "çˆ±å¿ƒ"),
    objects: t("emoji.objects", "ç‰©å“"),
    nature: t("emoji.nature", "è‡ªç„¶"),
  };

  const emojiGrid = (
    <div className="space-y-3">
      {/* Category tabs */}
      <div className="flex gap-1 overflow-x-auto pb-2 border-b">
        {emojiCategories.map((category, index) => (
          <button
            key={category.name}
            type="button"
            onClick={() => setActiveCategory(index)}
            className={`px-3 py-1.5 text-sm rounded-full whitespace-nowrap transition-colors ${
              activeCategory === index
                ? "bg-primary text-primary-foreground"
                : "bg-muted hover:bg-muted/80 text-muted-foreground"
            }`}
          >
            {categoryLabels[category.name]}
          </button>
        ))}
      </div>

      {/* Emoji grid */}
      <div className="grid grid-cols-8 gap-1">
        {emojiCategories[activeCategory].emojis.map((emoji, index) => (
          <button
            key={index}
            type="button"
            onClick={() => handleSelect(emoji)}
            className="w-9 h-9 flex items-center justify-center text-xl hover:bg-muted rounded-md transition-colors"
          >
            {emoji}
          </button>
        ))}
      </div>
    </div>
  );

  const triggerButton = (
    <button
      type="button"
      onClick={() => setOpen(true)}
      className="flex items-center gap-1.5 text-muted-foreground hover:text-foreground transition-colors"
    >
      <Smile className="h-5 w-5" />
      <span className="text-sm">{t("community.emoji", "è¡¨æƒ…")}</span>
    </button>
  );

  // Mobile: Drawer
  if (isMobile) {
    return (
      <>
        {triggerButton}
        <Drawer open={open} onOpenChange={setOpen}>
          <DrawerContent className="h-[50vh]">
            <DrawerHeader className="pb-2">
              <DrawerTitle>{t("emoji.title", "é€‰æ‹©è¡¨æƒ…")}</DrawerTitle>
            </DrawerHeader>
            <ScrollArea className="flex-1 px-4 pb-4">
              {emojiGrid}
            </ScrollArea>
          </DrawerContent>
        </Drawer>
      </>
    );
  }

  // Desktop: Popover
  return (
    <Popover open={open} onOpenChange={setOpen}>
      <PopoverTrigger asChild>{triggerButton}</PopoverTrigger>
      <PopoverContent className="w-80 p-3" align="start" side="top">
        {emojiGrid}
      </PopoverContent>
    </Popover>
  );
};

export default EmojiPicker;
