import { useState } from "react";
import { useNavigate } from "react-router-dom";
import { useTranslation } from "react-i18next";
import { Heart, Images, Play, Store, Calendar } from "lucide-react";
import { Avatar, AvatarFallback, AvatarImage } from "@/components/ui/avatar";
import { Badge } from "@/components/ui/badge";
import { CommunityPost, useLikePost } from "@/hooks/useCommunityPosts";
import { useAuth } from "@/contexts/AuthContext";
import { useLocalizedContent } from "@/hooks/useLocalizedContent";

interface PostGridCardProps {
  post: CommunityPost;
  showAssociations?: boolean;
}

const PostGridCard = ({ post, showAssociations = false }: PostGridCardProps) => {
  const { t } = useTranslation();
  const { user } = useAuth();
  const navigate = useNavigate();
  const { getLocalizedField } = useLocalizedContent();
  const likePost = useLikePost();
  const [imageError, setImageError] = useState(false);

  const handleLike = (e: React.MouseEvent) => {
    e.preventDefault();
    e.stopPropagation();
    if (!user) return;
    likePost.mutate({ postId: post.id, liked: post.user_liked || false });
  };

  const handleCardClick = () => {
    navigate(`/community/${post.id}`);
  };

  const handleRestaurantClick = (e: React.MouseEvent) => {
    e.preventDefault();
    e.stopPropagation();
    if (post.restaurant?.id) {
      navigate(`/restaurant/${post.restaurant.id}`);
    }
  };

  const handleActivityClick = (e: React.MouseEvent) => {
    e.preventDefault();
    e.stopPropagation();
    if (post.activity?.id) {
      navigate(`/activity/${post.activity.id}`);
    }
  };

  // Strip HTML tags for plain text preview
  const getPlainTextContent = (html: string) => {
    const doc = new DOMParser().parseFromString(html, "text/html");
    return doc.body.textContent || "";
  };

  const content = getLocalizedField(post, "content");
  const plainContent = getPlainTextContent(content);
  const hasImages = post.image_urls && post.image_urls.length > 0;
  const hasVideo = !!post.video_url;
  const hasMedia = hasImages || hasVideo;
  const imageCount = post.image_urls?.length || 0;

  // Get localized names
  const restaurantName = post.restaurant 
    ? getLocalizedField(post.restaurant, "name") 
    : null;
  const activityTitle = post.activity 
    ? getLocalizedField(post.activity, "title") 
    : null;

  return (
    <div 
      className="group cursor-pointer rounded-lg overflow-hidden bg-card border border-border/50 hover:shadow-lg transition-all duration-300 hover:-translate-y-1"
      onClick={handleCardClick}
    >
      {/* Media Section - Only show first image, no swipe */}
      {hasMedia && (
        <div className="relative aspect-[4/5] overflow-hidden">
          {/* Display cover image or video frame */}
          {hasImages ? (
            !imageError ? (
              <img
                src={post.image_urls![0]}
                alt=""
                className="w-full h-full object-cover group-hover:scale-105 transition-transform duration-500"
                onError={() => setImageError(true)}
              />
            ) : (
              <div className="w-full h-full bg-muted flex items-center justify-center">
                <span className="text-xs text-muted-foreground">
                  {t("common.imageLoadError", "å›¾ç‰‡åŠ è½½å¤±è´¥")}
                </span>
              </div>
            )
          ) : hasVideo ? (
            <video
              src={post.video_url}
              className="w-full h-full object-cover"
              preload="metadata"
              muted
              playsInline
            />
          ) : null}
          
          {/* Video indicator - show play button for video posts */}
          {hasVideo && (
            <div className="absolute inset-0 flex items-center justify-center pointer-events-none">
              <div className="w-12 h-12 rounded-full bg-black/50 backdrop-blur-sm flex items-center justify-center">
                <Play className="h-5 w-5 text-white fill-white ml-0.5" />
              </div>
            </div>
          )}
          
          {/* Multiple images indicator - only show if no video and multiple images */}
          {!hasVideo && imageCount > 1 && (
            <div className="absolute top-2 right-2 bg-black/60 text-white text-xs px-2 py-0.5 rounded-full z-10 flex items-center gap-1">
              <Images className="h-3 w-3" />
              <span>{imageCount}</span>
            </div>
          )}
        </div>
      )}

      {/* Content Section */}
      <div className="p-3">
        {/* Associations - only show when showAssociations is true */}
        {showAssociations && (restaurantName || activityTitle) && (
          <div className="flex flex-wrap gap-1.5 mb-2">
            {restaurantName && (
              <Badge 
                variant="secondary" 
                className="text-xs cursor-pointer hover:bg-secondary/80 gap-1"
                onClick={handleRestaurantClick}
              >
                <Store className="h-3 w-3" />
                <span className="truncate max-w-[120px]">{restaurantName}</span>
              </Badge>
            )}
            {activityTitle && (
              <Badge 
                variant="secondary" 
                className="text-xs cursor-pointer hover:bg-secondary/80 gap-1"
                onClick={handleActivityClick}
              >
                <Calendar className="h-3 w-3" />
                <span className="truncate max-w-[120px]">{activityTitle}</span>
              </Badge>
            )}
          </div>
        )}

        {/* Text Content */}
        <p className={`text-sm font-medium line-clamp-2 leading-relaxed ${hasMedia ? '' : 'min-h-[60px]'}`}>
          {plainContent}
        </p>

        {/* Author Row */}
        <div className="flex items-center justify-between mt-3">
          <div className="flex items-center gap-2 min-w-0 flex-1">
            <Avatar className="h-5 w-5 flex-shrink-0">
              <AvatarImage src={post.author?.avatar_url || undefined} />
              <AvatarFallback className="text-[10px]">
                {post.author?.display_name?.charAt(0) || "?"}
              </AvatarFallback>
            </Avatar>
            <span className="text-xs text-muted-foreground truncate">
              {post.author?.display_name}
            </span>
          </div>

          {/* Like Button */}
          <button
            className={`flex items-center gap-1 text-xs ${
              post.user_liked ? "text-red-500" : "text-muted-foreground"
            } hover:text-red-500 transition-colors`}
            onClick={handleLike}
            disabled={!user}
          >
            <Heart
              className={`h-4 w-4 ${post.user_liked ? "fill-current" : ""}`}
            />
            <span>{post.likes_count || 0}</span>
          </button>
        </div>
      </div>
    </div>
  );
};

export default PostGridCard;
